*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  background-color: #000;
}

.card {
  height: 100px;
  -webkit-user-select: none;
  user-select: none;
  background-color: #122;
  border: 2px solid #0000;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  font-family: Alegreya, sans-serif;
  font-size: 5vh;
  display: flex;
  box-shadow: 0 2px 5px #00000080;
}

.card:nth-child(2n) {
  height: 200px;
}

.card:nth-child(2n+1) {
  height: 300px;
}

